import Endpoint from '@/services/Endpoint'

export default {
  bankFunctions (payload) {
    return Endpoint().get('/s1/bank/bankFunction', {
      params: payload
    })
  },
  createBankFunction (payload) {
    return Endpoint().post('/s1/bank/bankFunction', payload)
  },
  modifyBankFunction (payload) {
    return Endpoint().patch('/s1/bank/bankFunction', payload)
  }
}
